.info-table {
  width: 100%;
}

.info-table, .info-table td, .info-table th {
  border-collapse: collapse;
  border: 1px solid #cccccc;
  color: #444444;
  margin-top: 0.25rem;
  padding-right: 0.5rem;
  padding: 0.25rem;
  text-align: left;
  vertical-align: top;
}

.info-table-monospace {
  font-family: Menlo, Monaco, "Courier New", monospace;
  font-size: 0.88em;
}

code {
  color: rgb(16, 114, 160);
}
p {
  color: rgb(102, 226, 220);
}

.title {
  color: rgb(255, 0, 0)
}